<script>
export default {
  name: "Notice",
  data() {
    return {
      active: false,
      buttonTxt: "",
    };
  },
  methods: {
    toggle() {
      this.active = !this.active;
      if (this.active) {
        this.buttonTxt = "";
      } else {
        this.buttonTxt = "";
      }
    },
    beforeEnter(element) {
      requestAnimationFrame(() => {
        if (!element.style.height) {
          element.style.height = "0px";
        }

        element.style.display = null;
      });
    },
    enter(element) {
      requestAnimationFrame(() => {
        requestAnimationFrame(() => {
          element.style.height = `${element.scrollHeight}px`;
        });
      });
    },
    afterEnter(element) {
      element.style.height = null;
    },
    beforeLeave(element) {
      requestAnimationFrame(() => {
        if (!element.style.height) {
          element.style.height = `${element.offsetHeight}px`;
        }
      });
    },
    leave(element) {
      requestAnimationFrame(() => {
        requestAnimationFrame(() => {
          element.style.height = "0px";
        });
      });
    },
    afterLeave(element) {
      element.style.height = null;
    },
  },
};
</script>
<style lang="scss">
#notice {
