                >{{ item.question }}
              </h2>
              <div class="answer">
                <p>
                  <input
                    type="radio"
                    :id="item.ansID.id1"
                    :name="item.name"
                    v-on:change="keepAnswer"
                    value="A"
                  />
                  <span class="icon">A</span>
                  <label for="item.name">{{ item.answer.ans1 }}</label>
                </p>
                <p>
                  <input
                    type="radio"
                    :id="item.ansID.id2"
                    :name="item.name"
                    v-on:change="keepAnswer"
                    value="B"
                  />
                  <span class="icon">B</span>
                  <label :for="item.name">{{ item.answer.ans2 }}</label>
                </p>
                <p>
                  <input
                    type="radio"
                    :id="item.ansID.id3"
                    :name="item.name"
                    v-on:change="keepAnswer"
                    value="C"
                  />
                  <span class="icon">C</span>
                  <label :for="item.name">{{ item.answer.ans3 }}</label>
                </p>
                <p>
                  <input
                    type="radio"
                    :id="item.ansID.id4"
                    :name="item.name"
                    v-on:change="keepAnswer"
                    value="D"
                  />
                  <span class="icon">D</span>
                  <label :for="item.name">{{ item.answer.ans4 }}</label>
                </p>
              </div>
              <a :href="item.AnotherLink" class="link">{{
                item.AnotherText
              }}</a>
            </li>
          </ul>
        </form>
        <div class="center">
          <button
            class="btn center"
            @click="
              sendKnowledge();
              keepAnswer();
            "
          >
            
          </button>
        </div>
      </article>
    </div>
  </section>
</template>

<script>
