          },
          AnotherLink: "https://www.tmnewa.com.tw/b2c_v2/contents/house.html",
          AnotherText: "* ",
        },
        {
          name: "Q10",
          ansID: {
            id1: "ans10-1",
            id2: "ans10-2",
            id3: "ans10-3",
            id4: "ans10-4",
          },
          question: "?",
          answer: {
            ans1: "!",
            ans2: "",
