          },
          AnotherLink: "https://b2c.tmnewa.com.tw/portal/insurance/travel/taiwan/",
          AnotherText: "* ",
        },
      ],
      userData: {},
      token: "",
    };
  },

  mounted() {
    this.getVal();
  },
  methods: {
    qaToggle(item) {
      item.qaShow = !item.qaShow;
    },

    sendKnowledge: _.debounce(function () {
      var self = this;
      const axios = require("axios").default;
      var knowledgeData = $("#knowledgeData").serializeArray();
      //check
      axios.post(
        Common.Config.Api.Activity.checkAnswer,
        {
          answerList: knowledgeData,
        }
      ).then(function (response) {
        if (response.data.code == 200) { // check
          sessionStorage.setItem("knowledgeData", JSON.stringify(knowledgeData));
            
          var userData = JSON.parse(sessionStorage.getItem("jwtInfo"));
          if (userData) { // 
            var token = userData.token;
            axios.post(
              Common.Config.Api.Activity.searchReply, //
              { actionId: "ques71" },
              {
                headers: {
                  Authorization: token,
                },
              }
            ).then(function (response) {
              sessionStorage.removeItem("knowledgeData");
              if (response.data.data == true) { //
                Swal.fire({
                  text: "",
                  confirmButtonText: "",
                  customClass: {
                    container: "remember",
                  },
                });
              } else if (response.data.data == false) { //
                axios.post(
                  Common.Config.Api.Activity.recordAction, //
                  { actionId: "ques71" },
                  {
                    headers: {
                      Authorization: token,
                    },
                  }
                ).then(function () {

                  Swal.fire({
                    title: "",
                    html: "<p></p><div class='award'><p></p></div>",
                    confirmButtonText: "",
                    customClass: {
                      container: "correct",
                    },
                  }).then(function () {
                    // window.location.href = "/";
                    self.$router.push('/');
                    dos.beaconSend(4,2,1,7,'','','_','')
                  });
                });
              }
            });
          } else { //
            Swal.fire({
              title: "",
              html: "<p></p><div class='award'><p>LINE POINT 50</p><p></p></div><div><p>/</p><p></p></div>",
              confirmButtonText: "",
              cancelButtonText: "",
              showCancelButton: true,
              customClass: {
                container: "correct",
              },
            }).then(
              function () {
                window.location.hash = "/#login";
                //self.$router.push('/#login');  //hashtag
                dos.beaconSend(4,2,1,7,'','','','')
                
              },
              function () {
                window.location.hash = "/#login";
                //self.$router.push('/#login'); //hashtag
                dos.beaconSend(4,2,1,7,'','','','')
              }
            );
          }
        } else if (response.data.code == 400) { //check
          Swal.fire({
            title: "()",
            confirmButtonText: "",
            customClass: {
              container: "remember",
            },
          });
        } else if (response.data.code == 404) { //check
          Swal.fire({
            title: "",
            html: "<p></p><div class='award'><p>LINE POINT 50</p><p></p></div>",
            confirmButtonText: "",
            cancelButtonText: "",
            showCancelButton: true,
            customClass: {
              container: "error",
            },
          }).then((result) => {
            if (result.isConfirmed) {
              // window.location.href = "/";
              self.$router.push('/');
            }
          });
        }
      });
    }, 2000),

    keepAnswer() {
      $("input[type=radio]").each(function () {
        localStorage.setItem(
          "radio_" + $(this).attr("id"),
          JSON.stringify({ checked: this.checked })
        );
      });
    },

    getVal() {
      $("input[type=radio]").each(function () {
        var state = JSON.parse(
          localStorage.getItem("radio_" + $(this).attr("id"))
        );

        if (state) this.checked = state.checked;
      });
    },

  },
};
</script>
<style lang="scss">
#knowledge {
  padding-top: 100px;
  background-image: url("../assets/class-bg.svg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-height: 100vh;
  background-size: cover;
  @media screen and (max-width: $pad) {
    background-image: none;
  }
  .container {
    width: 1000px;
    @media screen and (max-width: $large) {
      width: 90%;
    }
    @media screen and (max-width: $small) {
      width: 100%;
    }
    .teacher {
      position: fixed;
      bottom: 0;
      right: 5%;
      @media screen and (max-width: $large) {
        width: 15%;
        right: 0;
      }
      @media screen and (max-width: $small) {
        display: none;
      }
    }
  }
  .blackBoard {
    img {
      display: block;
      margin: 0;
      width: 100%;
    }
    .blackBoardCon {
      background-image: url("../assets/blackboard.svg");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center top;
      width: 100%;
      overflow: hidden;
      @media screen and (max-width: $small) {
        background-image: url("../assets/blackboard-m.svg");
        background-size: 100%;
        background-repeat: repeat-y;
      }
      .boardTxt {
        color: #f7fff8;
        width: 70%;
        margin: 0 auto;
        @media screen and (max-width: $pad) {
          width: 80%;
          padding-bottom: 15px;
        }
        img {
          width: 300px;
          margin: 30px auto;
          @media screen and (max-width: $pad) {
            width: 200px;
            margin: 20px auto;
          }
          @media screen and (max-width: $small) {
            width: 150px;
          }
        }
        article {
          margin: 15px 0;
          span {
            color: #f9eb30;
          }
        }
      }
    }
  }
  #exam {
    background-color: #fff;
    background-image: url("../assets/qa-noto.svg");
    background-repeat: no-repeat;
    background-position: center top;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 30px;
    margin: 50px 0;
    box-shadow: 0 2px 9px #afafaf;
    .question {
      li {
        padding: 20px 0;
        border-bottom: 1px dashed #ccc;
        line-height: 35px;
        &:last-of-type {
          border: 0;
        }
        @media screen and (max-width: $small) {
          padding: 10px 0;
        }
        .Q {
          margin-bottom: 10px;
          font-size:16px;
          span {
            color: #308582;
            margin-right: 5px;
            font-weight: bold;
            font-family: Arial, Helvetica, sans-serif;
            &::after {
              content: ".";
            }
          }
        }
        .answer {
          font-weight: 400;
          .icon {
            width: 25px;
            height: 25px;
            display: inline-block;
            text-align: center;
            color: #fff;
            font-weight: bold;
            margin-left: 8px;
            line-height: 25px;
            background-size: contain;
            background-repeat: no-repeat;
            background-image: url("../assets/qa-icon.svg");
          }
          label {
            margin-left: 8px;
          }
        }
      }
    }
    .link {
      color: $blue;
    }
  }
}
.remember {
  .swal2-title {
    font-size: 22px;
  }
  .swal2-show {
    background-image: url(../assets/class-teacher.svg);
    background-position: right 10px bottom;
    background-size: 16%;
    background-repeat: no-repeat;
    padding: 50px 0;
    box-sizing: border-box;
  }
}
.correct {
  .swal2-show {
    background-color: #e4efe7;
    padding: 50px 0;

    &::before {
      content: url("../assets/pop-boy.svg");
      right: -45%;
      bottom: -40%;
      position: absolute;
      @media screen and (max-width: $small) {
        display: none;
      }
    }
  }
  .swal2-html-container {
    font-weight: bold;
  }
  .swal2-title {
    color: $blue;
  }
  .award {
    padding: 10px 20px;
    box-sizing: border-box;
    border: 2px solid $blue;
    background: #fff;
    display: inline-block;
    border-radius: 10px;
    color: $blue;
    margin: 5px auto;
  }
}
.error {
  .swal2-show {
    background-color: #fff2f3;
    padding: 50px 0;
    &::before {
      content: url("../assets/pop-girl.svg");
      right: -20%;
      bottom: -30%;
      position: absolute;
      @media screen and (max-width: $small) {
        display: none;
      }
    }
    .swal2-html-container {
      font-weight: bold;
    }
    .swal2-title {
      color: $pink;
    }
    .award {
      padding: 10px 20px;
      color: $pink;
      margin: 5px auto;
    }
  }
}
</style>
