<script>
export default {
  name: "policy",
  mounted(){
    this.check();
  },
  methods:{
      check(){
        $(".agree-check").addClass("disable");
        $("#isAgree1").attr("disabled", true);
        $("#isAgree2").attr("disabled", true);
        $("#isAgree3").attr("disabled", true);

        this.scroll($("#policyDiv1"),$("#isAgree1"));
        this.scroll($("#policyDiv2"),$("#isAgree2"));
        this.scroll($("#policyDiv3"),$("#isAgree3"));
      
      },
      scroll(Div,checkBox){
          Div.on('scroll', function () {
            var scrollHeight = ($(this).prop('scrollHeight') - $(this).height()) * 0.9;
            if ($(this).scrollTop() >= scrollHeight) {
                checkBox.attr("disabled", false).removeClass("disable");
            }
        });
      }
  }
  
};
</script>

<style lang="scss">
.policy{
